<template>
  <div class="express_detail_border">
    <div class="express_company">
      <div>
        <span v-if="logisticsStatus === 0" class="express_company_name">二四半日达</span>
        <span v-if="logisticsStatus === 1" class="express_company_name">二四即时达</span>
        <span v-else class="express_company_name">{{ expressCompanyName }}</span>
        <span>运单号：</span>
        <span>{{ wbNo }}</span>
      </div>
      <p class="remark">如果发现快递单号有误或快递费用未支付，请联系<span class="courier">收件员</span></p>
    </div>
    <h4 v-if="logs.length" class="mt-3">{{ logs[logs.length-1].state === 7 ? '已签收' : '配送中' }}</h4>
    <div style="height: 300px;">
      <el-steps direction="vertical" :active="logs.length-1" finish-status="wait">
        <el-step v-for="(item, i) in logs" :key="i" :description="item.detail" />
      </el-steps>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExpressCmp',

  props: {
    expressCompanyName: {
      type: String,
      default: ''
    },
    wbNo: {
      type: String,
      default: ''
    },
    logisticsStatus: {
      type: Number,
      default: 100 // 0半日达 1即时达
    }
  },

  data () {
    return {
      logs: [], // 物流记录
    }
  },

  methods: {
    // api---------------------------------
    // 查询物流信息
    apiWaybill () {
      if (this.logisticsStatus === 100) {
        return false
      }
      this.$logisticsHttp.get(`/woho/myOrder/query24?no=${this.wbNo}&type=${this.logisticsStatus}`)
        .then((res) => {
          if (res.data.success) {
            this.logs = res.data.data.logs.filter((item) => item.state > 0)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  },

  created () {
    this.apiWaybill()
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;
  .express_detail_border{
    border: 1px solid #bfbfbf;
    border-radius: 10px;
    width: 90%;
    padding: 15px;
    margin: 27px auto;
    .express_company{
      font-size: 14px;
      .express_company_name{
        font-size: 16px;
        font-weight: bold;
        margin-right: 41px;
      }
      .remark{
        color: #999999;
        .courier{
          color: $themColor;
        }
      }
    }
  }
</style>
